@use "base/mixins" as m;
@use "base/variables" as v;

.nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: v.$separacion;
  @include m.tablet {
    flex-direction: row;
    max-width: min-content;
    margin-top: 0;
  }

  a {
    text-align: center;
    color: v.$blanco;
    text-transform: uppercase;
    font-weight: 700;
    padding: 0.5rem 0;
    @include m.tablet {
      padding: 0 0.5rem 1rem 0.5rem;
    }
    &:hover {
      color: v.$secundario;
    }
    &.activo {
      color: v.$secundario;
    }
  }
}
